.img-grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: center;
    align-items: center;
    margin: 1em 0.2em 1em;
    gap: 0.2em;
  }


  .img-wrap img{
    width: 100%;
    height: 200px;
    object-fit: cover;
    box-shadow: 0 4px 4px -5px black;
    margin-bottom: 1em;
    cursor: pointer;
  }