nav {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255,213,25);
    padding: 0.8rem 0.5rem;
    height: 3.5rem;
    position: sticky;
    top: 0;
    border-bottom: 1px solid black;
    margin-bottom: 0.5rem;
    z-index: 10;
}

.left-side {
    width: 100%;
}

.logo-nav {
    padding-left: 3rem;
}

.nav-icon {
    height: auto;
    width: 30px; 
    padding: 0 0.1rem;
}

.shoe {
    height: 40px;
    width: auto;
}

.giftbox{
    height: 27px;
    width: auto;
}

nav #close .nav-links{
    display: none;
}

nav #close .logo-nav{
    display: flex;
    height: 50px; 
}

nav #open .nav-links{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    animation: slide-in .8s ease-in-out;
}

@keyframes slide-in {
    0% {
      transform: translateX(-400px)
    }
    100% {
      transform: translateX(0px);
    }
}

nav #open .logo-nav {
   display: none;
}

.home {
    width: 40px;
}

.nav-btn {
    display: flex;
    border: none;
    background-color: rgb(255,213,25);
    padding-right: 1.2rem;
    cursor: pointer;
    
}

@media only screen and (min-width: 600px) {

    .left-side {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .nav-links {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 25rem;
        padding-right: 2.5rem;
    }

    nav #open .nav-links{
        display: flex;
        animation: none;
    }

    nav #close .nav-links {
        display: flex;
        animation: none;
    }

    nav #open .logo-nav {
        display: flex;
        height: 50px;
    }

    .nav-icon {
        height: auto;
        width: 35px; 
        
    }

    .home {
        width: 45px;
    }

    .shoe {
        height: 45px;
        width: auto;
    }
    
    .giftbox{
        height: 32px;
        width: auto;
    }

    .nav-btn {
        display: none;

    }
}